<template>
  <div class="homeChild">
    <van-nav-bar
      title="智能揽投"
      border
    ></van-nav-bar>
    <div id="map"></div>
    <div class="model">
      <van-row class="row">
        <van-col class="title" span="8">总件量</van-col>
        <van-col class="title" span="8">已投</van-col>
        <van-col class="title" span="8">待投</van-col>
      </van-row>
      <van-row class="row">
        <van-col class="text" span="8">19</van-col>
        <van-col class="text" span="8">15</van-col>
        <van-col class="text" span="8">4</van-col>
      </van-row>
    </div>
    <div class="buttons">
      <van-button @click="openScan(1)" class="button left-button" type="default">下段扫描</van-button>
      <van-button @click="openScan(2)" class="button right-button" type="default">装车扫描</van-button>
    </div>
  </div>
</template>
<script>
    export default {
      name: "test",
      data(){
        return {
          map:{}
        }
      },
      mounted(){
        this.initMap()
      },
      methods:{
        initMap(){
          this.map = new BMap.Map('map') // 创建地图实例
          var point = new BMap.Point(116.391641, 40.068351) // 创建点坐标
          this.map.centerAndZoom(point, 15) // 初始化地图，设置中心点坐标和地图级别
        },
        openScan(){
          this.$router.push('scanPage')
          this.plugins.openScan((err, text)=>{
            if(err){
              console.log(err)
              if(err.name == 'SCAN_CANCELED'){
                this.$toast('已取消扫描');
              }
            } else {
              // The scan completed, display the contents of the QR code:
              this.plugins.openVibrate(500)
              this.$toast(text);
              this.$router.goBack()
            }
          })
        },
        open(){
         // this.$get('test/testkuayu').then(res => {console.log(res)})

        }
      }
    }
</script>

<style scoped lang="scss">
.homeChild{
  #map{
    width: 100%;
    height: 100%;
  }
  .model{
    text-align: center;
    width: 343px;
    height: 46px;
    position: absolute;
    top: 53px;
    left: 16px;
    background: #fff;
    box-shadow: 0 2px 4px 0;
    border-radius: 6px;
    .row{
      height: 23px;
      line-height: 23px;
    }
    .title{
      font-size: 12px;
      color: #A7A7A7;
    }
    .text{
      font-size: 18px;
      color: #0091FF;
    }
  }
  .buttons{
    position: absolute;
    bottom: 81px;
    .button{
      width: 92px;
      height: 30px;
      line-height: 30px;
      background: #0091FF;
      border-radius: 6px;
      color: #fff;
      font-size: 13px;
      text-align: center;
    }
    .left-button{
      margin-left: 73px;
      margin-right: 47px;
    }
  }
}
</style>



